.news-main{
    padding: 30px 0px;
}
.news{
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.news_title {
    color: #093891;
    font-size: 45px;
    font-family: serif;
  }
.news-main>span{
    margin-left: 430px;
    size: 40px;
}
.subnew{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.LineDiv {
    background-color: #95C11E;
    border: 3px;
    width: 2px;
    height: 200px;
}
.dayDiv{
    display: flex;
    flex-direction: column;
    justify-content:center ;
    text-align: center;
    font-size: 20px;
}

.hathiimg{
    position: relative;
    bottom: 55px;
}
.hathicaption{
    position: absolute;
    margin-top: 120px;
    margin-left: 65px;
}
.btnDiv{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.news-btn{
    border: 2px solid #95C11E;
    border-radius: 5px;
    color: #95C11E;
    padding: 7px 35px ;
}