.Home_main {
    position: relative;
}

.home_img {
    Width: 1260px;
    height: 450px;
}

.card {
    border-radius: 10px;
    width: 290px;
    height: 155px;
    position: absolute;
    bottom: 215px;
    right: 5px;
    opacity: 90%;
    background-color: #093891;
    align-items: center;
}
.card-title{
    font-size: 30px;
    color: white;
    line-height: 37.5px;
    font-weight: 400;
}
.card-text{
    display: flex;
    flex-direction: row;
    gap: 10px;
    font-size: 20px;
    font-weight: 300;
    line-height: 40px;
    color: white;
}
.vector{
    color: white;
}
.btn{
    background-color: #95C11E;
    color: white;
    margin-left: 5px;
    padding: 5px 30px;
    border-radius: 5px;
    margin-top: 8px;
}
.social_icons{
    display: flex;
}
.facebook{
    position: absolute;
    top: 20px;
    right: 120px;
    color: #093891 ;
}
.twitter{    
    position: absolute;
    top: 25px;
    right: 70px;
    color: #093891 ;
}
.linkedin{
    position: absolute;
    top: 20px;
    right: 20px;
    color: #093891 ;
}
.welcome{
    width: auto;
    align-items: center;
    text-align: center;
    margin: 10px 0px;
    background-color: #95C11E;
    padding: 30px 190px;
}
.welcome_title{
    color: #093891;
    font-size: 50px;
    line-height: 50px;
    font-weight: 400;
    font-family:serif;
}
.welcome_content{
    color: white;
    font-size: 21px;
}