.facts {
  text-align: center;
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: center;
}

.facts_title {
  color: #093891;
  font-size: 45px;
  font-family: serif;
}

.lines {
  background-color: #95C11E;
  border: 3px;
  width: 55px;
  height: 2px;
}


.facts-main {
  background-color: white;
  padding: 0px 48px;
  justify-content: center;
}

.services-heading {
  display: flex;
  justify-content: center;
}

.service-p {
  font-size: 30px;
  font-weight: 500;
}

.service-p b {
  color: #4DB8EF;
  font-weight: 500;
}

.facts-row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.row-items {
  justify-content: space-between;
  margin: 20px 60px;
}

.facts-img-icon {
  background-color: #95C11E;
  width: 170px;
  height: 170px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.facts-img-iconn {
  background-color: #093891;
  width: 170px;
  height: 170px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.contntp {
  margin-left: 5px;
  font-size: large;
  text-align: center;
}

.contnt {
  align-items: center;
}

.grup5-img {
  width: 70px;
  margin-top: 45px;
    margin-left: 48px;
}

.award-img {
  width: 46px;
  margin-top: 45px;
  margin-left: 60px;
}

.grup6-img {
  margin-top: 55px;
  margin-left: 45px;
  width: 72px;
}

.grup7-img {
  margin-top: 54px;
  margin-left: 63px;
  width: 45px;
}


@media screen and (max-width:480px) {
  .facts-row {
    display: block;
    align-items: center;
    text-align: center;
  }

  .facts-img-icon {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
  }

  .grup5-img,
  .grup7-img,
  .grup6-img,
  .award-img {
    width: 100%;
    display: flex;
    margin: 0 30px;
    justify-content: center;
  }

  .row-items {
    margin-bottom: 40px;
  }
}