.achieve {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0px;
}

.achieve_title {
    color: #093891;
    font-size: 45px;
    font-family: serif;
    margin: 20px 0px;
}

.achieve-main {
    padding: 0px 30px;
    width: 1280px;
    height: 520px;
    position: relative;
    background-image: url('../Assets/achieveimg.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.achiveImagesGroup {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.acvImg1 {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    text-align: center;
    font-size: 13px;
    
}

